"use client"

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Progress } from '@/components/ui/progress'
import { useAuth } from '@/contexts/auth-context'
import Link from 'next/link'

// Mock data
const mockStats = {
  totalTransactions: 12,
  activeTransactions: 3,
  completedTransactions: 8,
  totalVolume: 2450000, // AOA
  walletBalance: 125000, // AOA
  pendingDisputes: 1,
}

const mockRecentTransactions = [
  {
    id: 'TR001',
    title: 'Venda de Laptop Dell',
    amount: 350000,
    currency: 'AOA',
    status: 'funded',
    counterparty: 'Maria Santos',
    createdAt: '2024-01-20',
    type: 'selling'
  },
  {
    id: 'TR002',
    title: 'Compra de Smartphone',
    amount: 180000,
    currency: 'AOA',
    status: 'pending',
    counterparty: 'João Silva',
    createdAt: '2024-01-19',
    type: 'buying'
  },
  {
    id: 'TR003',
    title: 'Serviços de Design',
    amount: 75000,
    currency: 'AOA',
    status: 'released',
    counterparty: 'Ana Costa',
    createdAt: '2024-01-18',
    type: 'selling'
  },
]

const mockTasks = [
  {
    id: '1',
    title: 'Completar verificação KYC',
    description: 'Envie seus documentos para verificação de identidade',
    priority: 'high',
    completed: false,
    href: '/dashboard/documents'
  },
  {
    id: '2',
    title: 'Configurar autenticação 2FA',
    description: 'Aumente a segurança da sua conta',
    priority: 'medium',
    completed: false,
    href: '/dashboard/settings'
  },
  {
    id: '3',
    title: 'Adicionar método de pagamento',
    description: 'Configure sua conta bancária ou cartão',
    priority: 'medium',
    completed: true,
    href: '/dashboard/wallet'
  },
]

export default function DashboardPage() {
  const { user } = useAuth()

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('pt-AO', {
      style: 'currency',
      currency: 'AOA',
      minimumFractionDigits: 0,
    }).format(amount)
  }

  const getStatusBadge = (status: string) => {
    const variants = {
      pending: 'secondary',
      funded: 'default',
      released: 'default',
      disputed: 'destructive',
      cancelled: 'secondary',
    } as const

    const labels = {
      pending: 'Pendente',
      funded: 'Financiado',
      released: 'Liberado',
      disputed: 'Em Disputa',
      cancelled: 'Cancelado',
    }

    return (
      <Badge variant={variants[status as keyof typeof variants] || 'secondary'}>
        {labels[status as keyof typeof labels] || status}
      </Badge>
    )
  }

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'high': return 'text-red-600'
      case 'medium': return 'text-yellow-600'
      case 'low': return 'text-green-600'
      default: return 'text-muted-foreground'
    }
  }

  return (
    <div className="space-y-6">
      {/* Welcome Section */}
      <div>
        <h1 className="text-3xl font-bold">Bem-vindo, {user?.name}!</h1>
        <p className="text-muted-foreground mt-2">
          Aqui está um resumo da sua atividade na plataforma EscrowAngola
        </p>
      </div>

      {/* Stats Cards */}
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Transações Ativas</CardTitle>
            <span className="text-2xl">💰</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{mockStats.activeTransactions}</div>
            <p className="text-xs text-muted-foreground">
              +2 desde o mês passado
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Volume Total</CardTitle>
            <span className="text-2xl">📊</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{formatCurrency(mockStats.totalVolume)}</div>
            <p className="text-xs text-muted-foreground">
              +15% desde o mês passado
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Saldo da Carteira</CardTitle>
            <span className="text-2xl">💳</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{formatCurrency(mockStats.walletBalance)}</div>
            <p className="text-xs text-muted-foreground">
              Disponível para transações
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Taxa de Sucesso</CardTitle>
            <span className="text-2xl">✅</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">96%</div>
            <p className="text-xs text-muted-foreground">
              {mockStats.completedTransactions} de {mockStats.totalTransactions} concluídas
            </p>
          </CardContent>
        </Card>
      </div>

      <div className="grid gap-6 lg:grid-cols-2">
        {/* Recent Transactions */}
        <Card>
          <CardHeader>
            <div className="flex items-center justify-between">
              <div>
                <CardTitle>Transações Recentes</CardTitle>
                <CardDescription>
                  Suas últimas atividades na plataforma
                </CardDescription>
              </div>
              <Button variant="outline" size="sm" asChild>
                <Link href="/dashboard/transactions">Ver Todas</Link>
              </Button>
            </div>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {mockRecentTransactions.map((transaction) => (
                <div key={transaction.id} className="flex items-center justify-between p-3 border rounded-lg">
                  <div className="flex items-center space-x-3">
                    <div className="w-10 h-10 bg-primary/10 rounded-full flex items-center justify-center">
                      <span className="text-primary">
                        {transaction.type === 'buying' ? '🛒' : '💼'}
                      </span>
                    </div>
                    <div>
                      <p className="font-medium text-sm">{transaction.title}</p>
                      <p className="text-xs text-muted-foreground">
                        {transaction.type === 'buying' ? 'Comprando de' : 'Vendendo para'} {transaction.counterparty}
                      </p>
                    </div>
                  </div>
                  <div className="text-right">
                    <p className="font-medium text-sm">{formatCurrency(transaction.amount)}</p>
                    {getStatusBadge(transaction.status)}
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>

        {/* Tasks & Actions */}
        <Card>
          <CardHeader>
            <CardTitle>Tarefas Pendentes</CardTitle>
            <CardDescription>
              Complete estas ações para melhorar sua experiência
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {mockTasks.map((task) => (
                <div key={task.id} className="flex items-start space-x-3 p-3 border rounded-lg">
                  <div className={`w-4 h-4 rounded-full mt-1 ${
                    task.completed ? 'bg-green-500' : 'bg-muted'
                  }`} />
                  <div className="flex-1">
                    <div className="flex items-center justify-between">
                      <p className={`font-medium text-sm ${
                        task.completed ? 'line-through text-muted-foreground' : ''
                      }`}>
                        {task.title}
                      </p>
                      <span className={`text-xs ${getPriorityColor(task.priority)}`}>
                        {task.priority === 'high' ? 'Alta' : 
                         task.priority === 'medium' ? 'Média' : 'Baixa'}
                      </span>
                    </div>
                    <p className="text-xs text-muted-foreground mt-1">
                      {task.description}
                    </p>
                    {!task.completed && (
                      <Button variant="outline" size="sm" className="mt-2" asChild>
                        <Link href={task.href}>Completar</Link>
                      </Button>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* KYC Status */}
      {user?.kycStatus !== 'verified' && (
        <Card className="border-yellow-200 bg-yellow-50 dark:border-yellow-800 dark:bg-yellow-950">
          <CardHeader>
            <CardTitle className="flex items-center space-x-2">
              <span>⚠️</span>
              <span>Verificação de Identidade Pendente</span>
            </CardTitle>
            <CardDescription>
              Complete sua verificação KYC para acessar todas as funcionalidades
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm mb-2">Progresso da verificação:</p>
                <Progress value={user?.kycStatus === 'pending' ? 50 : 0} className="w-64" />
              </div>
              <Button asChild>
                <Link href="/dashboard/documents">Completar KYC</Link>
              </Button>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Quick Actions */}
      <Card>
        <CardHeader>
          <CardTitle>Ações Rápidas</CardTitle>
          <CardDescription>
            Acesso rápido às funcionalidades mais utilizadas
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
            <Button className="h-20 flex-col space-y-2" asChild>
              <Link href="/dashboard/transactions/create">
                <span className="text-2xl">➕</span>
                <span>Nova Transação</span>
              </Link>
            </Button>
            
            <Button variant="outline" className="h-20 flex-col space-y-2" asChild>
              <Link href="/dashboard/wallet">
                <span className="text-2xl">💳</span>
                <span>Gerenciar Carteira</span>
              </Link>
            </Button>
            
            <Button variant="outline" className="h-20 flex-col space-y-2" asChild>
              <Link href="/dashboard/messages">
                <span className="text-2xl">💬</span>
                <span>Mensagens</span>
              </Link>
            </Button>
            
            <Button variant="outline" className="h-20 flex-col space-y-2" asChild>
              <Link href="/dashboard/reports">
                <span className="text-2xl">📊</span>
                <span>Relatórios</span>
              </Link>
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
